/* Navigation.css */

nav {
  background-color: #0d1424;
  padding: 10px;
  opacity: 1000;
}

.menu-toggle {
  display: none;
  cursor: pointer;
  color: #fff;
  font-size: 1.2em;
  margin-bottom: 10px;
  text-align: center;
}

.menu-toggle span{
  font-size: 24px;
  font-weight: 600;
  padding-left: 20px;
  


  
}

ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
}

li {
  margin: 0 15px;
}

a {
  text-decoration: none;
  color: #fff;

  font-size: 1.1em;
  transition: color 0.3s ease;
}

a:hover {
  color: #ffd700;
}

/* Responsive Styles */
@media screen and (max-width: 600px) {
  .menu-toggle {
    display: block;
  }

  ul {
    flex-direction: column;
    align-items: center;
    display: none;
  }

  ul.active {
    display: flex;
  }

  li {
    margin: 10px 0;
  }
}
