/* Skills.css */

.skills-container {
  background-color: #f7f7f7;
  /* background-image: url('../background-images/bg3.jpg'); */
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 20px 20px 20px;
}

.skill-card p{
  color: #444;
  font-size: 1.2rem;
  font-weight: 400;
}

.skills-category {
  background-color: #11062c;
  padding: 30px;
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  border-radius: 20PX;
  justify-content: space-around;
  align-items: center;
}

.skill-card {
  background-color: #fff;
  /* background-image: linear-gradient(180deg, #19122c, #4a3578);   */
  padding: 15px;
  width: 90px;
  height: 90px;
  text-align: center;
  border-radius: 8px;
  margin-bottom: 15px;
  transition: transform 0.3s ease-in-out;
}

.skill-card:hover {
  transform: scale(1.05);
  filter: grayscale(50%);  
}

.skill-card img {
  width: 40px;
  height: 40px;   
}

@media screen and (max-width: 576px) {

  .skill-card p{
    color: #444;
    font-size: 1rem;
    font-weight: 400;
  }
  
  .skill-card {
    background-color: #f1f1f1;
    padding: 10px;
    width: 60px;
    height: 60px;
    text-align: center;
    border-radius: 8px;   
    transition: transform 0.3s ease;
  }
  .skill-card img {
    width: 35px;
    height: 35px;   
  }

  .skill-card p{
    font-size: 12px;
  }

  .skill-card img:hover{
    filter: grayscale(100%);
  }

}
