/* Projects.css */

.projects {
  background-color: #3333;
  padding: 50px;
  text-align: center;
}

.heading-container {
  display: flex;
  align-items: center;
  animation: slideTopToBottom 1.5s ease-in-out;
}

.icon {
  width: 30px; /* Adjust the icon size as needed */
  margin-right: 10px;
}


.project-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px; /* Add margin for spacing between cards */
}

.project-card {
  width: calc(33.333% - 20px); /* Adjust the card width based on your preference */
  margin-bottom: 20px; /* Add margin at the bottom for spacing between rows */
  box-sizing: border-box;
  padding: 10px;  
  background-color: #f7f7f7;
}

.project-image {
  width: 100%; /* Make the image fill the container */
  height: auto; /* Maintain aspect ratio */
  border-radius: 8px; /* Add rounded corners to the image */
}
.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}
 
.project-title {
  color: #444;
  font-size: 1.5em;
  text-shadow: #000 1px 1px;
  margin-bottom: 10px;
}
.project-details {
  padding: 10px;
  box-sizing: border-box;
}

.project-description {
  color: #333;
  font-size: 1em;
} 


.read-more-link {
  font-size: 12px;  
  text-align: center;
  color: #007bff; /* Adjust the link color as needed */
  text-decoration: none;
  border: 1px solid #333;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  margin-top: 20px;
  margin-bottom: 20px;
  box-sizing: border-box;
  
}


/* Projects.css */


.project-card {
border: 1px solid #ddd; /* Add a border to separate project cards */
border-radius: 8px;
overflow: hidden; /* Hide overflow from images */
margin-bottom: 20px;
}

.project-title,
.project-description {
padding: 10px;
}

/* Responsive Styles */
@media only screen and (max-width: 768px) {
  .projects {
    padding: 30px;
  }

  .project-card {
    flex-basis: calc(50% - 20px);
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 600px) {
  .project-card {
    flex-basis: calc(100% - 20px);
  }
}
